import styled from "styled-components";

const TopSectionContainer = styled.div`
  display: flex;
  justify-content: center;
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0;
  top: 0;
  left: 0;
  background-color: #1756dd32;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 99;
`

const Logo = styled.h1`
  margin: 0;
  color: #fffd;
  font-weight: 800;
  font-size: 45px;
  text-align: center;
`

const Slogan = styled.h4`
  margin: 0;
  color: #fffa;
  font-weight: 700;
  font-size: 25px;
  max-width: 435px;
  margin-top: 10px;
  text-align: center;
`

const Paragraph = styled.p`
  display: block;
  margin: 0;
  margin-top: 2em;
  color: #fffd;
  font-size: 16px;
  line-height: 1.5;
  font-weight: 500;
  max-width: 555px;
  text-align: center;
`

const Enter = styled.button`
  outline: none;
  border: none;
  color: #ffff;
  background-color: #5c6ac48d;
  font-size: 18px;
  font-weight: 600;
  border-radius: 8px;
  padding: 10px 2em;
  margin-top: 3em;
  cursor: pointer;
  border: 3px solid transparent;
  transition: all 350ms ease-in-out;

  a {
    color: #fffb;
    text-decoration: none;
    transition: all 350ms ease-in-out;
  }

  &:hover {
    background-color: #5c6ac4ff;
  }
`

export function TopSection() {
    return (
        <TopSectionContainer>
            <Logo>Thought Repository</Logo>
            <Slogan>애플리케이션 개발에 대한 기술 지식과 생각을 기록하는 생각 저장소</Slogan>
            <Paragraph>
                '여호와여 주께서 하신 일이 어찌 그리 많은지요. 주께서 지혜로 그들을 다 지으셨으니 주께서 지으신 것들이 땅에 가득하니이다.
                거기에는 크고 넓은 바다가 있고 그 속에는 생물 곧 크고 작은 동물들이 무수하니이다.'<br/>
                ✜ 시편  104:24-25 ✜
                <br/><br/>
                'O LORD, what a variety of things you have made! In wisdom you have made them all. The earth is full of your creatures.
                Here is the ocean, vast and wide, teeming with life of every kind, both large and small.'<br/>
                ✜ Psalms  104:24-25 ✜
            </Paragraph>
            <a href='world'><Enter>Enter</Enter></a>
        </TopSectionContainer>
    )
}
