import { useRef } from "react";
import { useFrame, useLoader } from "@react-three/fiber";
import { Stars } from "@react-three/drei";
import * as THREE from "three";

import EarthDayMap from "../../assets/textures/earth_daymap.jpg";
import EarthNormalMap from "../../assets/textures/earth_normal_map.jpg";
import EarthSpecularMap from "../../assets/textures/earth_specular_map.jpg";
import EarthCloudsMap from "../../assets/textures/earth_clouds.jpg";

export function Earth() {
    const [colorMap, normalMap, specularMap, cloudsMap] = useLoader(
        THREE.TextureLoader,
        [EarthDayMap, EarthNormalMap, EarthSpecularMap, EarthCloudsMap]
    )

    const earthRef = useRef()
    const cloudsRef = useRef()

    useFrame(({ clock }) => {
        const elapsedTime = clock.getElapsedTime()
        earthRef.current.rotation.y = elapsedTime / 50
        cloudsRef.current.rotation.x = elapsedTime / 90
        cloudsRef.current.rotation.y = elapsedTime / 45
    })

    return (
        <>
            {/* <ambientLight intensity={1} /> */}
            <pointLight color="#f6f3ea" position={[1, 0, 5]} intensity={.8} />
            <Stars
                radius={200}
                depth={60}
                count={10000}
                factor={10}
                saturation={10}
                fade={true}
            />
            <mesh ref={cloudsRef} position={[0, 0, 3.205]}>
                <sphereGeometry args={[1.005, 64, 64]} />
                <meshPhongMaterial
                    map={cloudsMap}
                    opacity={0.5}
                    depthWrite={true}
                    transparent={true}
                    side={THREE.DoubleSide}
                />
            </mesh>
            <mesh ref={earthRef} position={[0, 0, 3.2]}>
                <sphereGeometry args={[1, 64, 64]} />
                <meshPhongMaterial specularMap={specularMap} />
                <meshStandardMaterial
                    map={colorMap}
                    normalMap={normalMap}
                    metalness={0.4}
                    roughness={0.7}
                />
            </mesh>
        </>
    )
}
